import React, { useEffect, useState } from 'react';
import Chart from 'react-apexcharts';
import PropTypes from 'prop-types';

const baseConfig = {
  series: [0],
  options: {
    chart: {
      height: 200,
      type: 'radialBar',
    },
    colors: ['#1ab7ea'],
    labels: ['Cricket'],
  },
};

const RadialChart = ({ title, value, fromColor, toColor }) => {
  const [config, setConfig] = useState(baseConfig);
  useEffect(() => {
    setConfig({
      series: [value],
      options: {
        chart: {
          height: 350,
          type: 'radialBar',
        },
        plotOptions: {
          radialBar: {
            hollow: {
              size: '70%',
            },
            dataLabels: {
              name: {
                color: '#D0D3D4',
                fontSize: '18px',
              },
              value: {
                color: '#D0D3D4',
                fontSize: '20px',
                show: true,
              },
            },
          },
        },
        colors: [fromColor],
        labels: [title],
        fill: {
          type: 'gradient',
          gradient: {
            shade: 'dark',
            type: 'vertical',
            gradientToColors: [toColor],
            stops: [0, 100],
          },
        },
        stroke: {
          lineCap: 'round',
        },
      },
    });
  }, [title, value, toColor, fromColor]);
  return (
    <div>
      <Chart
        options={config.options}
        series={config.series}
        type="radialBar"
        height="260"
      />
    </div>
  );
};

RadialChart.propTypes = {
  title: PropTypes.string.isRequired,
  value: PropTypes.number.isRequired,
  fromColor: PropTypes.string.isRequired,
  toColor: PropTypes.string.isRequired,
};

export default RadialChart;
